import { Builder } from '../../Builder'

export interface InputVenueMessageContentPayload {
    latitude: number
    longitude: number
    title: string
    address: string
    foursquare_id?: string
    foursquare_type?: string
    google_place_id?: string
    google_place_type?: string
}

export class InputVenueMessageContent extends Builder<InputVenueMessageContentPayload> {

    constructor() {
        super({
            latitude: 0,
            longitude: 0,
            title: '',
            address: ''
        })
    }

    public latitude(latitude: number): this {
        this.packet.latitude = latitude
        return this
    }

    public longitude(longitude: number): this {
        this.packet.longitude = longitude
        return this
    }

    public title(title: string): this {
        this.packet.title = title
        return this
    }

    public address(address: string): this {
        this.packet.address = address
        return this
    }

    public foursquareId(id?: string): this {
        this.packet.foursquare_id = id
        return this
    }

    public foursquareType(type?: string): this {
        this.packet.foursquare_type = type
        return this
    }

    public googlePlaceId(id?: string): this {
        this.packet.google_place_id = id
        return this
    }

    public googlePlaceType(type?: string): this {
        this.packet.google_place_type = type
        return this
    }

}
